/* This was made with GlassGenerator.netlify.app */ 

.glass-container{
    width: 70%;
    height: 60%;
    position: fixed;
    color: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 4rem;
    gap: 20px;
    border-radius: 10px;
    backdrop-filter: blur(7px);
    background-color: rgba(255,255,255, 0.16);
    box-shadow: rgba(0, 0, 0, 0.3) 2px 8px 8px;
    border: 2px rgba(255,255,255,0.9) solid;
    border-bottom: 2px rgba(40,40,40,0.35) solid;
    border-right: 2px rgba(40,40,40,0.35) solid;
}
//-------------------------------
//  Loading-splash
//-------------------------------
.splash-bg{
    display: none;
    body.appear &{
        display: block;
        content: "";
        position: fixed;
        z-index: 1000;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        transform: scaleX(0);
        background-color: $color-trans-bg;
        animation: PageAnime 1.2s ease-in-out forwards;
    }
}

//-------------------------------
//  Humberger-menu
//-------------------------------
.navbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    height: 16rem;
    @include sp{
        height: 8rem;
    }
    .menu{
        width: 100%;
        height: 100%;
        margin: 0 auto;
        padding: 0 6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color-white;
        @include sp{
            padding: 0 2.9rem;
        }
        @include tab{
            padding: 0 4.5rem;
        }
    }
    .list-menu{
        height: 4rem;
        width: 4rem;
        cursor: pointer;
        margin-left: auto;
        transform: scale(1.0);
        transition: transform 0.5s ease;
        @include sp{
            width: 2.2rem;
            height: 2.2rem;
        }
        &.menu-active{
            transform: rotateZ(180deg) scale(1.1);
            transform-origin: center;
            transition: transform 0.5s ease;
        }
    }
    .list-menu-top{
        display: flex;
        justify-content: space-between;
        margin-bottom: 0.8rem;
        @include sp{
            margin-bottom: 0.6rem;
        }
    }
    .list-menu-bottom{
        display: flex;
        justify-content: space-between;
    }
    .box{
        width: 1.6rem;
        height: 1.6rem;
        background-color: $color-white;
        @include sp{
            width: 0.8rem;
            height: 0.8rem;
        }
    }
}

