                        width: 2rem;
                        height: 0.2rem;
                    }
                }
                span{
                    display: block;
                    width: 4.8rem;
                    height: 0.4rem;
                    background-color: $color-white;
                    position: absolute;
                    top: 50%;
                    transition: opacity 0.5s ease;
                    opacity: 1;
                    @include tab{
                        width: 2rem;
                        height: 0.2rem;
                    }
                }
            }
        }
        .accordion-text{
            max-height: 0;
            overflow: hidden;
            opacity: 0;
            transition: max-height 0.5s ease, opacity 0.5s ease;
            &.is-open{
                max-height: 100vh;
                opacity: 1;
                transition: max-height 0.5s ease, opacity 0.5s ease;
            }
        }
        .is-active{
            .toggle-btn{
                &:after{
                    transform: rotateZ(0deg);
                    transition: transform 0.3s ease;
                }
                span{
                    opacity: 0;
                    transition: opacity 0.5s ease;
                }
            }
        }
