//iphone CSS
.mockup__iphone{
    text-align: center;
    div{
        display: block;
    }
      
    *, *:after, *:before {
        -webkit-transition: all 0.3s ease;
        -moz-transition:    all 0.3s ease;
        -ms-transition:     all 0.3s ease;
        -o-transition:      all 0.3s ease;
    }
      
    .device {
        display: inline-block;
        position: relative;
    }
      
    .bt1 {
        top:11.1rem;
        height:3.3rem;
        @include tab{
            top: 6.9375rem;
            height: 2.0625;
        }
    }
      
    .bt2 {
        top: 18.6rem;
        @include tab{
            top: 11.625rem;
        }
    }
      
    .bt3 {
        top: 24.5rem;
        @include tab{
            top: 15.3125rem;
        }
    }
      
    .screen {
        border: 2px solid #000;
        border-radius: 2px; 
        background: #fff;
        clear: both;
        position:relative;
        overflow:hidden;
    }
    .case {
        background: #2b2b2b;
        position: relative;
        border-radius: 4.5rem;
        box-shadow: 0px 0px 3px 1px #000 inset;
        border: 2px solid #4a4a4a;
        @include tab{
            border-radius: 2.4rem;
        }
    }
      
    .border {
        border: 0.5rem solid #3d3d3d;
        border-radius: 5.1rem;
        @include tab{
            border-radius: 2.72rem;
            border: 0.3rem solid #3d3d3d;
        }
    }
      
      
    .camera {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top:2.5rem;
        width: 0.8rem;
        height: 0.8rem;
        display: block;
        border-radius: 0.8rem;
        background: #454545;
        border: 0.4rem solid #000;
        @include tab{
            top: 1.5625rem;
            width: 0.5rem;
            height: 0.5rem;
            border-radius: 0.5rem;
        }
    }
      
    .speaker {
        width: 6.2rem;
        height: 0.8rem;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top:5.9rem;
        display: block;
        border-radius: 0.8rem;
        background: #454545;
        border: 0.4rem solid #000; 
        @include tab{
            width: 3.875rem;
            height: 0.5rem;
            top: 3.2rem;
            border-radius: 0.5rem;
            border: 0.1rem solid #000;
        }
    }
      
    .homebt {
        width: 5.6rem;
        height: 5.6rem;
        display: block;
        z-index: 1;
        position: relative;
        background: #404040;
        border-radius: 50%;
        position: absolute;
        bottom: 1.4rem;
        left: 50%;
        transform: translateX(-50%);
        cursor: pointer;
        @include tab{
            bottom: 0.875rem;
        }
    }
      
    .homebt:before {
        content:' ';
        width: 2rem;
        height: 2rem;
        display: block;
        margin: 0 auto;
        border: 2px solid #6e6e6e;
        background: #404040;
        border-radius: 3px;
        z-index: 2;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        @include tab{
            border: 1px solid #6e6e6e;
        }
    }
      
    /* iphone */
    .iphone .speaker {
        opacity: 1
    }
    
    .iphone .screen {
        width: 32rem;
        height: 56rem;
        margin: 8.3rem 2.2rem 8.2rem;
        overflow-y: scroll;
        scrollbar-width: none;
        @include tab{
            width: 20rem;
            height: 36rem;
            margin: 5rem 1.375rem 5rem;
        }
        &:-webkit-scrollbar{
            display: none;
        }
        img{
            width: 100%;
            height: auto;
        }
    }
      
    .iphone .homebt {
        width: 5.6rem;
        height: 5.6rem;
        @include tab{
            width: 3.5rem;
            height: 3.5rem;
        }
    }
      
    .iphone .homebt:before {
        width: 2rem;
        height: 2rem;
        @include tab{
            width: 1.25rem;
            height: 1.25rem;
        }
    }
      
    .iphone .sidebt {
        position: absolute;
        width: 0.3rem;
        height: 2.5rem;
        background: #4d4d4d;
        border: 1px solid #3e3e3e;
        left: -0.4rem;
        border-top-left-radius:3px;
        border-bottom-left-radius:3px;
        @include tab{
            width: 0.1875rem;
            height: 1.5625rem;
        }
    }
      
    .iphone .pwrbt {
        position: absolute;
        top:-0.4rem;
        right:6.3rem;
        background: #4d4d4d;
        border: 1px solid #3e3e3e; 
        border: 1px solid #3e3e3e;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        width: 5.9rem;
        height: 0.3rem;
        @include tab{
            width: 3.6875rem;
            right: 3.9375rem;
            height: 1.5625rem;
            top: -0.25rem;
        }
    }
}

