            transform: translateX(0);
        }
        .head-title{
            margin: 0 auto 5.5rem auto;
        }
        .head-text{
            width: 100%;
        }
    }
    .sns-block{
        bottom: 16%;
        &:before{
            content: '';
            position: absolute;
            bottom: 185%;
            left: 50%;
            width: 1px;
            height: 12rem;
            background-color: $color-white;
            animation: scroll-lead 3s cubic-bezier(1, 0, 0, 1) infinite;
            @include tab{
                height: 18rem;
            }
        }
    }
    footer{
        position: fixed;
        bottom: 0%;
        left: 50%;
        transform: translateX(-50%);
    }
}

//-------------------------------
//  404 Not Found
//-------------------------------
.notFound{
    position: relative;
    .background-movie{
        width: 100vw;
        height: 100vh;
        position: relative;
        object-fit: cover;
        &:after{
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background-color: $color-dark;
            opacity: 0.7;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
    .main-visual{
        position: absolute;
        top: 0;
        left: 0;
        &__inner{
            text-align: center;
        }
        &__header{
            justify-content: center;
            transform: translateX(0);
        }
        .head-title{
            margin: 0 auto 5.5rem auto;
        }
        .head-text{
            width: 100%;
        }
    }
    footer{
        position: fixed;
        bottom: 0%;
        left: 50%;
        transform: translateX(-50%);
    }
    .error {
        text-align: center;
        animation: noise 2s linear infinite;
        overflow: default;
        position: relative;
        &:after{
            content: '';
            background-image: url('../images/title-notfound-blue.svg');
            background-size: contain;
            background-repeat: no-repeat;
            animation: noise-1 .2s linear infinite;
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            display: block;
            width: 25.5rem;
            height: 11.7rem;
            @include tab{
                width: 13rem;
                height: 7.5rem;
            }
        }
        &:before{
            content: '';
            background-image: url('../images/title-notfound-red.svg');
            background-size: contain;
            background-repeat: no-repeat;
            animation: noise-1 .2s linear infinite;
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            display: block;
            width: 25.5rem;
            height: 11.7rem;
            @include tab{
                width: 13rem;
                height: 7.5rem;
            }
        }
      }      
    .info {
        display: inline-block;
        animation: noise-3 1s linear infinite;
    }
}

.scroll-container{
    width: 100%;
    position: absolute;
    overflow: hidden;
    will-change: transform;
    backface-visibility: hidden;
}